	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"/2018-7-28-9357506b88f537a435b5268e6c84520d.jpg\" alt=\"Surf Queen\"></p>\n<p>This one is actually from a couple weeks ago. The model was wearing mermaid earrings and a necklace referencing St Christopher, the patron saint of surfers, she said. She said she was going for a regal expression, so I added the tiara.</p>\n<p>Amazing how much making art with surf imagery awakened my own memories of when I used to surf in the warmer, gentler waves of Florida.</p>","excerpt":"This one is actually from a couple weeks ago. The model was wearing mermaid earrings and a necklace referencing St Christopher, the patron saint of surfers, she said. She said she was going for a regal expression, so I added the tiara. Amazing how…","frontmatter":{"date":"July 28, 2018","path":"/blog/2018/7/28","title":"Surf Queen","category":"life, the universe and everything"}}},"pathContext":{}}

